import React, { useState, useRef, useCallback } from 'react';
import EnhancedModal from './EnhancedModal';
import ModalCard from './ModalCard';
import { motion, AnimatePresence } from 'framer-motion';
import {
  UserPlus, UserMinus, ListCheck, Upload, Download, Search,
  FileText, CheckCircle, XCircle, AlertTriangle, Calendar, Lock, Key
} from 'lucide-react';
import { generatePseudonym, spliceAddress } from '../../utils/nameGenerator';
import { Button } from '../LandingPage';
import { parseAddressesFromFile, exportAddresses, parseAddressesFromContent } from '../../utils/addressParser';
import AuthenticationGate from '../AuthenticationGate';
import { useToast } from '../Toast';

const accentColor = '#f59e0b'; // Amber/gold for admin

export default function AdminWhitelistModal({
  isOpen,
  onClose,
  whitelist,
  addressInput,
  setAddressInput,
  onAdd,
  onRemove,
  onBulkAdd,
  loading
}) {
  const { toast } = useToast();
  const [isDragOver, setIsDragOver] = useState(false);
  const [importResult, setImportResult] = useState(null);
  const [searchQuery, setSearchQuery] = useState('');
  const fileInputRef = useRef(null);

  // Auth gate state for protected actions
  const [showAuthGate, setShowAuthGate] = useState(false);
  const [pendingAction, setPendingAction] = useState(null);
  const [pendingActionData, setPendingActionData] = useState(null);
  const [isAuthenticatedForSession, setIsAuthenticatedForSession] = useState(false);

  // Request authentication for a protected action
  const requestAuth = (action, data = null) => {
    if (isAuthenticatedForSession) {
      executeProtectedAction(action, data);
    } else {
      setPendingAction(action);
      setPendingActionData(data);
      setShowAuthGate(true);
    }
  };

  // Validate address(es) before requesting auth for add/remove
  const validateAndRequestAuth = (action) => {
    if (!addressInput.trim()) {
      toast.error('Please enter an address', { title: 'Validation Error' });
      return;
    }

    const result = parseAddressesFromContent(addressInput);

    if (result.valid.length === 0) {
      // No valid addresses found
      if (result.invalid.length > 0) {
        toast.error(`Invalid address format: ${result.invalid[0]}`, { title: 'Validation Error' });
      } else {
        toast.error('No valid Ethereum addresses found', { title: 'Validation Error' });
      }
      return;
    }

    // For remove action, check if address exists in whitelist
    if (action === 'remove') {
      const addressToRemove = result.valid[0];
      if (!whitelist.includes(addressToRemove)) {
        toast.warning(`Address not in whitelist: ${spliceAddress(addressToRemove)}`, { title: 'Not Found' });
        return;
      }
    }

    // Address(es) valid, now request auth
    requestAuth(action);
  };

  // Execute protected action after authentication
  const executeProtectedAction = (action, data) => {
    switch (action) {
      case 'add':
        handleAddAddresses();
        break;
      case 'remove':
        if (data) onRemove(data);
        else onRemove();
        break;
      case 'bulkImport':
        if (data) processFile(data);
        break;
      case 'export':
        if (data) {
          exportAddresses(whitelist, 'admin-whitelist', data);
          toast.success(`Exported ${whitelist.length} addresses as ${data.toUpperCase()}`, { title: ' Export Complete' });
        }
        break;
      default:
        break;
    }
  };

  // Handle successful authentication
  const handleAuthSuccess = () => {
    setIsAuthenticatedForSession(true);
    setShowAuthGate(false);
    if (pendingAction) {
      executeProtectedAction(pendingAction, pendingActionData);
      setPendingAction(null);
      setPendingActionData(null);
    }
  };

  // Parse and add multiple addresses
  const handleAddAddresses = () => {
    const result = parseAddressesFromContent(addressInput);
    if (result.valid.length === 0) {
      onAdd();
    } else if (result.valid.length === 1) {
      onAdd();
      toast.success('Address added to admin list', { duration: 3000 });
    } else {
      onBulkAdd(result.valid);
      toast.success(`${result.valid.length} addresses added to admin list`, {
        title: ' Bulk Add Complete',
        duration: 4000
      });
    }
    if (result.invalid?.length > 0) {
      toast.warning(`${result.invalid.length} invalid address(es) skipped`, {
        title: 'Some Addresses Invalid',
        duration: 5000
      });
    }
    setImportResult(null);
  };

  // Process imported file
  const processFile = async (file) => {
    try {
      const result = await parseAddressesFromFile(file);

      // Filter out addresses already in whitelist
      const existingSet = new Set(whitelist.map(a => a.toLowerCase()));
      const newAddresses = result.valid.filter(a => !existingSet.has(a.toLowerCase()));
      const duplicateCount = result.valid.length - newAddresses.length;

      if (newAddresses.length > 0) {
        onBulkAdd(newAddresses);
        toast.success(`${newAddresses.length} address(es) imported successfully`, {
          title: ' Import Complete',
          duration: 4000
        });
      } else if (result.valid.length > 0 && duplicateCount > 0) {
        toast.info(`All ${duplicateCount} address(es) already in admin list`, {
          title: 'No New Addresses',
          duration: 4000
        });
      }
      if (result.invalid?.length > 0) {
        toast.warning(`${result.invalid.length} invalid address(es) skipped`, {
          title: 'Some Addresses Invalid',
          duration: 5000
        });
      }
      setImportResult(null);
    } catch (e) {
      console.error('File import error:', e);
      toast.error('Failed to import file. Please try again.', {
        title: 'Import Failed',
        duration: 5000
      });
      setImportResult(null);
    }
  };

  // Handle file drop
  const handleDrop = useCallback((e) => {
    e.preventDefault();
    setIsDragOver(false);
    const file = e.dataTransfer.files[0];
    if (file) requestAuth('bulkImport', file);
  }, [isAuthenticatedForSession]);

  // Handle file select
  const handleFileSelect = (e) => {
    const file = e.target.files[0];
    if (file) requestAuth('bulkImport', file);
    e.target.value = '';
  };

  // Filter whitelist by search
  const filteredWhitelist = whitelist.filter(addr => {
    const q = searchQuery.toLowerCase();
    return addr.toLowerCase().includes(q) || generatePseudonym(addr).toLowerCase().includes(q);
  });

  return (
    <>
      {/* Authentication Gate Overlay */}
      <AuthenticationGate
        isOpen={showAuthGate}
        onAuthenticated={handleAuthSuccess}
        onCancel={() => { setShowAuthGate(false); setPendingAction(null); setPendingActionData(null); }}
        accentColor={accentColor}
        title="Owner Authentication Required"
        description="Modifying the admin access list requires owner-level authentication."
      />

      <EnhancedModal
        isOpen={isOpen}
        onClose={onClose}
        title=" Admin Access Whitelist"
        subtitle="Extend Admin Panel Access"
        titleColor={accentColor}
        borderColor={`${accentColor}80`}
        shadowColor={`${accentColor}66`}
        scrollbarColor={accentColor}
      >
        {/* Auth Status Banner */}
        {isAuthenticatedForSession && (
          <div style={{
            padding: '8px 16px',
            background: 'rgba(34,197,94,0.15)',
            border: '1px solid rgba(34,197,94,0.4)',
            borderRadius: 8,
            marginBottom: 16,
            display: 'flex',
            alignItems: 'center',
            gap: 8,
            color: '#4ade80',
            fontSize: '0.9rem'
          }}>
             Authenticated for this session - modifications enabled
          </div>
        )}

        {/* Info Banner */}
        <div style={{
          padding: '12px 16px',
          background: 'rgba(251,191,36,0.1)',
          border: '1px solid rgba(251,191,36,0.3)',
          borderRadius: 8,
          marginBottom: 16,
          color: '#fcd34d',
          fontSize: '0.9rem'
        }}>
          <Key size={16} style={{ marginRight: 8, verticalAlign: 'middle' }} />
          <strong>Admin Access Control:</strong> Addresses added here will have access to the Admin Panel beyond the deployer wallet.
        </div>

        {/* Add Address Section */}
        <ModalCard
          icon={UserPlus}
          title="Add Admin Addresses"
          accentColor={accentColor}
          gradient={`linear-gradient(135deg, ${accentColor}26, ${accentColor}1a)`}
        >
          <div style={{ display: 'grid', gap: 12 }}>
            <textarea
              value={addressInput}
              onChange={e => setAddressInput(e.target.value)}
              placeholder="Add Ethereum addresses (0x...) separated by comma or new line"
              rows={3}
              style={{
                width: '100%',
                padding: '12px 16px',
                borderRadius: 8,
                border: `1px solid ${accentColor}80`,
                background: 'rgba(0,0,0,0.3)',
                color: 'white',
                fontSize: '1rem',
                boxSizing: 'border-box',
                resize: 'vertical',
                minHeight: 80,
                fontFamily: 'inherit'
              }}
            />
            <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
              <Button
                onClick={() => validateAndRequestAuth('add')}
                disabled={loading || !addressInput.trim()}
                style={{ display: 'inline-flex', alignItems: 'center', padding: '10px 16px' }}
              >
                {isAuthenticatedForSession ? <UserPlus size={16} style={{ marginRight: 6 }}/> : <Lock size={16} style={{ marginRight: 6 }}/>}
                Add Address(es)
              </Button>
              <Button
                onClick={() => validateAndRequestAuth('remove')}
                disabled={loading || !addressInput.trim()}
                style={{ display: 'inline-flex', alignItems: 'center', padding: '10px 16px' }}
              >
                {isAuthenticatedForSession ? <UserMinus size={16} style={{ marginRight: 6 }}/> : <Lock size={16} style={{ marginRight: 6 }}/>}
                Remove
              </Button>
            </div>
          </div>
        </ModalCard>

        {/* Bulk Import Section */}
        <ModalCard
          icon={Upload}
          title="Bulk Import"
          accentColor="#10b981"
          gradient="linear-gradient(135deg, rgba(16,185,129,0.15), rgba(5,150,105,0.15))"
        >
          <div
            onDragOver={(e) => { e.preventDefault(); setIsDragOver(true); }}
            onDragLeave={() => setIsDragOver(false)}
            onDrop={handleDrop}
            onClick={() => fileInputRef.current?.click()}
            style={{
              border: `2px dashed ${isDragOver ? '#10b981' : '#4b5563'}`,
              borderRadius: 12,
              padding: 24,
              textAlign: 'center',
              cursor: 'pointer',
              background: isDragOver ? 'rgba(16,185,129,0.1)' : 'transparent',
              transition: 'all 0.2s ease'
            }}
          >
            <Upload size={32} style={{ color: '#10b981', marginBottom: 8 }} />
            <p style={{ margin: 0, color: '#d1d5db' }}>
              Drop a file here or click to upload
            </p>
            <small style={{ color: '#6b7280' }}>Supports .txt, .csv, .json</small>
            <input
              ref={fileInputRef}
              type="file"
              accept=".txt,.csv,.json"
              onChange={handleFileSelect}
              style={{ display: 'none' }}
            />
          </div>
          <AnimatePresence>
            {importResult && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                exit={{ opacity: 0, height: 0 }}
                style={{ marginTop: 12 }}
              >
                <div style={{ padding: 12, background: 'rgba(0,0,0,0.3)', borderRadius: 8 }}>
                  {importResult.valid?.length > 0 && (
                    <div style={{ color: '#4ade80', marginBottom: 4 }}>
                      <CheckCircle size={14} style={{ marginRight: 6, verticalAlign: 'middle' }} />
                      {importResult.valid.length} address(es) imported successfully
                    </div>
                  )}
                  {importResult.invalid?.length > 0 && (
                    <div style={{ color: '#f87171', marginBottom: 4 }}>
                      <XCircle size={14} style={{ marginRight: 6, verticalAlign: 'middle' }} />
                      {importResult.invalid.length} invalid address(es) skipped
                    </div>
                  )}
                  {importResult.duplicates?.length > 0 && (
                    <div style={{ color: '#fcd34d' }}>
                      <AlertTriangle size={14} style={{ marginRight: 6, verticalAlign: 'middle' }} />
                      {importResult.duplicates.length} duplicate(s) skipped
                    </div>
                  )}
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </ModalCard>

        {/* Export Section */}
        <ModalCard
          icon={Download}
          title="Export Admin List"
          accentColor="#f59e0b"
          gradient="linear-gradient(135deg, rgba(245,158,11,0.15), rgba(217,119,6,0.15))"
        >
          <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
            <Button
              onClick={() => requestAuth('export', 'txt')}
              disabled={whitelist.length === 0}
              style={{ display: 'inline-flex', alignItems: 'center', padding: '10px 16px' }}
            >
              {isAuthenticatedForSession ? <FileText size={16} style={{ marginRight: 6 }} /> : <Lock size={16} style={{ marginRight: 6 }} />}
              Export as TXT
            </Button>
            <Button
              onClick={() => requestAuth('export', 'csv')}
              disabled={whitelist.length === 0}
              style={{ display: 'inline-flex', alignItems: 'center', padding: '10px 16px' }}
            >
              {isAuthenticatedForSession ? <FileText size={16} style={{ marginRight: 6 }} /> : <Lock size={16} style={{ marginRight: 6 }} />}
              Export as CSV
            </Button>
            <Button
              onClick={() => requestAuth('export', 'json')}
              disabled={whitelist.length === 0}
              style={{ display: 'inline-flex', alignItems: 'center', padding: '10px 16px' }}
            >
              {isAuthenticatedForSession ? <FileText size={16} style={{ marginRight: 6 }} /> : <Lock size={16} style={{ marginRight: 6 }} />}
              Export as JSON
            </Button>
          </div>
          {!isAuthenticatedForSession && (
            <p style={{ color: '#9ca3af', fontSize: '0.85rem', marginTop: 8, marginBottom: 0 }}>
               Export requires owner authentication
            </p>
          )}
        </ModalCard>

        {/* Admin Ledger */}
        <ModalCard
          icon={ListCheck}
          title={`Admin Ledger (${whitelist.length} addresses)`}
          accentColor={accentColor}
          gradient={`linear-gradient(135deg, ${accentColor}26, ${accentColor}1a)`}
        >
          {/* Search */}
          <div style={{ marginBottom: 12, position: 'relative' }}>
            <Search size={16} style={{ position: 'absolute', left: 12, top: '50%', transform: 'translateY(-50%)', color: '#9ca3af' }} />
            <input
              value={searchQuery}
              onChange={e => setSearchQuery(e.target.value)}
              placeholder="Search by address or pseudonym..."
              style={{
                width: '100%',
                padding: '10px 12px 10px 36px',
                borderRadius: 8,
                border: `1px solid ${accentColor}60`,
                background: 'rgba(0,0,0,0.3)',
                color: 'white',
                fontSize: '0.95rem',
                boxSizing: 'border-box'
              }}
            />
          </div>

          {/* Address List */}
          <div style={{ maxHeight: 300, overflowY: 'auto', display: 'grid', gap: 8 }}>
            {filteredWhitelist.length === 0 ? (
              <p style={{ color: '#6b7280', textAlign: 'center', padding: 20 }}>
                {whitelist.length === 0 ? 'No admin addresses added yet' : 'No matching addresses'}
              </p>
            ) : filteredWhitelist.map((addr, i) => (
              <div
                key={addr}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'space-between',
                  padding: '10px 12px',
                  background: 'rgba(0,0,0,0.3)',
                  borderRadius: 8,
                  border: `1px solid ${accentColor}40`
                }}
              >
                <div style={{ flex: 1, minWidth: 0 }}>
                  <div style={{ color: '#fcd34d', fontWeight: 500, fontSize: '0.95rem' }}>
                    {generatePseudonym(addr)}
                  </div>
                  <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginTop: 2 }}>
                    <small style={{ color: '#9ca3af', fontFamily: 'monospace', fontSize: '0.8rem' }}>
                      {spliceAddress(addr)}
                    </small>
                    <small style={{ color: '#6b7280', display: 'flex', alignItems: 'center', gap: 4 }}>
                      <Calendar size={10} />
                      Recently added
                    </small>
                  </div>
                </div>
                <Button
                  onClick={() => requestAuth('remove', addr)}
                  size="sm"
                  style={{ flexShrink: 0, padding: '6px 12px' }}
                >
                  {isAuthenticatedForSession ? <UserMinus size={14} style={{ marginRight: 4 }}/> : <Lock size={14} style={{ marginRight: 4 }}/>}
                  Remove
                </Button>
              </div>
            ))}
          </div>
        </ModalCard>
      </EnhancedModal>
    </>
  );
}
