import React, { useState, useCallback } from 'react';
import EnhancedModal from './EnhancedModal';
import ModalCard from './ModalCard';
import { motion } from 'framer-motion';
import {
  TreePine, Download, Copy, Check, Lock,
  RefreshCw, Users, Sparkles, Info
} from 'lucide-react';
import { Button } from '../LandingPage';
import { buildMerkleTree, generateProof, exportMerkleData } from '../../utils/merkleTree';
import AuthenticationGate from '../AuthenticationGate';

export default function MerkleTreeGeneratorModal({
  isOpen,
  onClose,
  whitelist = [],
  whitelistType = 'breeding', // 'breeding' | 'dao'
  onRootGenerated,
  onSetRootOnChain // New prop for setting root on-chain
}) {
  const [merkleTree, setMerkleTree] = useState(null);
  const [copied, setCopied] = useState(false);
  const [showMycelialInfo, setShowMycelialInfo] = useState(false);
  const [verifyAddress, setVerifyAddress] = useState('');
  const [verifyResult, setVerifyResult] = useState(null);
  const [setRootResult, setSetRootResult] = useState(null); // { type: 'success' | 'error' | 'demo', message: string }
  const [isSettingRoot, setIsSettingRoot] = useState(false);

  // Auth gate state for protected actions
  const [showAuthGate, setShowAuthGate] = useState(false);
  const [pendingAction, setPendingAction] = useState(null); // 'export' | 'setRoot'
  const [isAuthenticatedForSession, setIsAuthenticatedForSession] = useState(false);
  const [isDemoMode, setIsDemoMode] = useState(true);

  // Request authentication for a protected action
  const requestAuth = (action) => {
    if (isAuthenticatedForSession) {
      // Already authenticated this session, execute directly
      executeProtectedAction(action);
    } else {
      setPendingAction(action);
      setShowAuthGate(true);
    }
  };

  // Execute protected action after authentication
  const executeProtectedAction = (action) => {
    if (action === 'export') {
      handleExport();
    } else if (action === 'setRoot') {
      handleSetRootOnChain();
    }
  };

  // Handle successful authentication
  const handleAuthSuccess = (demoMode) => {
    setIsAuthenticatedForSession(true);
    setIsDemoMode(demoMode);
    setShowAuthGate(false);
    if (pendingAction) {
      executeProtectedAction(pendingAction);
      setPendingAction(null);
    }
  };

  const handleGenerateTree = useCallback(() => {
    if (whitelist.length === 0) {
      return;
    }
    const tree = buildMerkleTree(whitelist);
    setMerkleTree(tree);
    if (onRootGenerated) {
      onRootGenerated(tree.root, whitelistType);
    }
    // Save to localStorage
    const data = exportMerkleData(tree, whitelistType);
    localStorage.setItem(`merkle_${whitelistType}_root`, JSON.stringify(data));
  }, [whitelist, whitelistType, onRootGenerated]);

  const handleCopyRoot = () => {
    if (merkleTree?.root) {
      navigator.clipboard.writeText(merkleTree.root);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  const handleVerifyAddress = () => {
    if (!merkleTree || !verifyAddress) return;
    const proof = generateProof(merkleTree, verifyAddress);
    setVerifyResult(proof);
  };

  const handleExport = () => {
    if (!merkleTree) return;
    const data = {
      ...exportMerkleData(merkleTree, whitelistType),
      addresses: merkleTree.addresses,
      proof_sample: merkleTree.addresses[0]
        ? generateProof(merkleTree, merkleTree.addresses[0])
        : null
    };
    const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `merkle_${whitelistType}_${Date.now()}.json`;
    a.click();
    URL.revokeObjectURL(url);
  };

  const handleSetRootOnChain = async () => {
    if (!merkleTree?.root || !onSetRootOnChain) return;

    // Clear previous result
    setSetRootResult(null);
    setIsSettingRoot(true);

    try {
      if (isDemoMode) {
        // Demo mode - show inline message instead of alert
        setSetRootResult({
          type: 'demo',
          message: `[DEMO MODE] Would set ${typeLabels[whitelistType] || whitelistType} Merkle root on-chain. Switch to Live Mode to deploy.`
        });
      } else {
        // Live mode - call the parent handler
        await onSetRootOnChain(merkleTree.root, whitelistType, isDemoMode);
        setSetRootResult({
          type: 'success',
          message: ` ${typeLabels[whitelistType] || whitelistType} Merkle root set on-chain successfully!`
        });
      }
    } catch (e) {
      setSetRootResult({
        type: 'error',
        message: `Failed to set Merkle root: ${e.message}`
      });
    } finally {
      setIsSettingRoot(false);
    }
  };

  const typeLabels = {
    breeding: 'Breeding Lab',
    dao: 'DAO Governance',
    admin: 'Admin Access',
    denylist: 'Global Denylist'
  };
  const typeColors = {
    breeding: '#a78bfa',
    dao: '#60a5fa',
    admin: '#f59e0b',
    denylist: '#ef4444'
  };
  const typeLabel = typeLabels[whitelistType] || 'Whitelist';
  const typeColor = typeColors[whitelistType] || '#a78bfa';

  return (
    <>
      {/* Authentication Gate Overlay */}
      <AuthenticationGate
        isOpen={showAuthGate}
        onAuthenticated={handleAuthSuccess}
        onCancel={() => { setShowAuthGate(false); setPendingAction(null); }}
        accentColor={typeColor}
        title="Steward Authentication Required"
        description={
          pendingAction === 'export'
            ? 'Exporting Merkle data requires steward-level access.'
            : 'Setting the Merkle root on-chain requires steward-level access.'
        }
      />

      <EnhancedModal
        isOpen={isOpen}
        onClose={onClose}
        title={` ${typeLabel} Merkle Generator`}
        subtitle="Generate cryptographic roots from the mycelial network"
        titleColor={typeColor}
        borderColor={`${typeColor}80`}
        shadowColor={`${typeColor}66`}
        scrollbarColor={typeColor}
      >
      {/* Whitelist Status */}
      <ModalCard
        icon={Users}
        title="Current Whitelist"
        accentColor={typeColor}
        gradient={`linear-gradient(135deg, ${typeColor}26, ${typeColor}1a)`}
      >
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <span style={{ color: '#d1d5db' }}>
            <strong style={{ color: typeColor, fontSize: '1.5rem' }}>{whitelist.length}</strong>
            {' '}addresses in the {typeLabel.toLowerCase()} whitelist
          </span>
          <Button
            onClick={() => setShowMycelialInfo(!showMycelialInfo)}
            style={{ padding: '8px 12px', background: 'rgba(167,139,250,0.2)', width: 'fit-content', minWidth: 'auto' }}
          >
            <Info size={16} style={{ marginRight: 6 }} />
            Learn More
          </Button>
        </div>
        {showMycelialInfo && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            style={{ marginTop: 16, padding: 16, background: 'rgba(0,0,0,0.3)', borderRadius: 8 }}
          >
            <p style={{ color: '#a78bfa', fontStyle: 'italic', margin: 0 }}>
              "The Merkle root is the primordial mycelium from which all identities emerge.
              Every proof is a return along the luminous fungal paths back to Source."
            </p>
          </motion.div>
        )}
      </ModalCard>

      {/* Generate Tree */}
      <ModalCard
        icon={TreePine}
        title="Generate Merkle Root"
        accentColor="#22c55e"
        gradient="linear-gradient(135deg, rgba(34,197,94,0.15), rgba(22,163,74,0.15))"
      >
        <div style={{ display: 'grid', gap: 16 }}>
          <Button
            onClick={handleGenerateTree}
            disabled={whitelist.length === 0}
            style={{ height: 48, fontSize: '1.1rem', width: 'fit-content', minWidth: 'auto', justifySelf: 'center' }}
          >
            <RefreshCw size={18} style={{ marginRight: 8 }} />
            Generate Mother Root from {whitelist.length} Addresses
          </Button>

          {merkleTree?.root && (
            <motion.div
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              style={{
                padding: 16,
                background: 'rgba(34,197,94,0.1)',
                border: '1px solid rgba(34,197,94,0.3)',
                borderRadius: 8
              }}
            >
              <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 12 }}>
                <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                  <Sparkles size={16} style={{ color: '#4ade80' }} />
                  <span style={{ color: '#4ade80', fontWeight: 600 }}>Mother Root Generated!</span>
                </div>
                <div style={{ display: 'flex', gap: 12, fontSize: '0.85rem' }}>
                  <span style={{ color: '#9ca3af' }}>
                    <Users size={14} style={{ marginRight: 4, verticalAlign: 'middle' }} />
                    {whitelist.length} addresses
                  </span>
                  <span style={{ color: '#a78bfa' }}>
                    <TreePine size={14} style={{ marginRight: 4, verticalAlign: 'middle' }} />
                    {merkleTree.nodeCount || (2 * whitelist.length - 1)} nodes
                  </span>
                </div>
              </div>
              <div style={{ display: 'flex', gap: 8, alignItems: 'center' }}>
                <code style={{
                  flex: 1,
                  padding: 12,
                  background: 'rgba(0,0,0,0.4)',
                  borderRadius: 6,
                  color: '#4ade80',
                  fontSize: '0.75rem',
                  wordBreak: 'break-all',
                  fontFamily: 'monospace'
                }}>
                  {merkleTree.root}
                </code>
                <Button onClick={handleCopyRoot} style={{ minWidth: 'auto', width: 'fit-content', padding: '8px 12px' }}>
                  {copied ? <Check size={14} /> : <Copy size={14} />}
                  {copied ? ' Copied' : ' Copy'}
                </Button>
              </div>
            </motion.div>
          )}
        </div>
      </ModalCard>

      {/* Verify Address */}
      {merkleTree?.root && (
        <ModalCard
          icon={Check}
          title="Verify Address Inclusion"
          accentColor="#06b6d4"
          gradient="linear-gradient(135deg, rgba(6,182,212,0.15), rgba(8,145,178,0.15))"
        >
          <div style={{ display: 'grid', gap: 12 }}>
            <input
              value={verifyAddress}
              onChange={(e) => setVerifyAddress(e.target.value)}
              placeholder="Enter address to verify (0x...)"
              style={{
                width: '100%',
                padding: '12px 16px',
                borderRadius: 8,
                border: '1px solid rgba(6,182,212,0.5)',
                background: 'rgba(0,0,0,0.3)',
                color: 'white',
                fontSize: '1rem',
                boxSizing: 'border-box'
              }}
            />
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Button onClick={handleVerifyAddress} disabled={!verifyAddress} style={{ width: 'fit-content', minWidth: 'auto' }}>
                Trace Mycelial Path
              </Button>
            </div>
            {verifyResult && (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                style={{
                  padding: 12,
                  background: verifyResult.valid ? 'rgba(34,197,94,0.2)' : 'rgba(239,68,68,0.2)',
                  border: `1px solid ${verifyResult.valid ? 'rgba(34,197,94,0.5)' : 'rgba(239,68,68,0.5)'}`,
                  borderRadius: 8,
                  color: verifyResult.valid ? '#4ade80' : '#fca5a5'
                }}
              >
                {verifyResult.valid
                  ? ' Address found in the mycelial network! Proof path contains ' + verifyResult.proof.length + ' nodes.'
                  : ' Address not found in current whitelist.'}
              </motion.div>
            )}
          </div>
        </ModalCard>
      )}

      {/* Export & Set On-Chain - Auth Protected */}
      {merkleTree?.root && (
        <ModalCard
          icon={Download}
          title="Export & Deploy"
          accentColor="#f59e0b"
          gradient="linear-gradient(135deg, rgba(245,158,11,0.15), rgba(217,119,6,0.15))"
        >
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 12 }}>
            {/* Auth-protected Export Button */}
            <Button
              onClick={() => requestAuth('export')}
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                gap: 8,
                width: 'fit-content',
                minWidth: 'auto'
              }}
            >
              {isAuthenticatedForSession ? (
                <Download size={16} />
              ) : (
                <Lock size={16} />
              )}
              Download Merkle Tree JSON
            </Button>

            {/* Auth-protected Set Root On-Chain Button */}
            {onSetRootOnChain && (
              <Button
                onClick={() => requestAuth('setRoot')}
                disabled={isSettingRoot}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  gap: 8,
                  width: 'fit-content',
                  minWidth: 'auto',
                  background: 'linear-gradient(135deg, rgba(34,197,94,0.3), rgba(22,163,74,0.2))',
                  border: '1px solid rgba(34,197,94,0.5)',
                  opacity: isSettingRoot ? 0.7 : 1
                }}
              >
                {isSettingRoot ? (
                  <RefreshCw size={16} style={{ animation: 'spin 1s linear infinite' }} />
                ) : isAuthenticatedForSession ? (
                  <Sparkles size={16} />
                ) : (
                  <Lock size={16} />
                )}
                {isSettingRoot ? 'Setting Root...' : 'Set Merkle Root On-Chain'}
              </Button>
            )}

            {/* Inline result message for set root operation */}
            {setRootResult && (
              <div style={{
                padding: '12px 16px',
                borderRadius: 8,
                marginTop: 8,
                background: setRootResult.type === 'success'
                  ? 'rgba(34,197,94,0.15)'
                  : setRootResult.type === 'error'
                    ? 'rgba(239,68,68,0.15)'
                    : 'rgba(59,130,246,0.15)',
                border: `1px solid ${
                  setRootResult.type === 'success'
                    ? 'rgba(34,197,94,0.4)'
                    : setRootResult.type === 'error'
                      ? 'rgba(239,68,68,0.4)'
                      : 'rgba(59,130,246,0.4)'
                }`,
                color: setRootResult.type === 'success'
                  ? '#4ade80'
                  : setRootResult.type === 'error'
                    ? '#f87171'
                    : '#60a5fa',
                fontSize: '0.9rem'
              }}>
                {setRootResult.message}
              </div>
            )}

            <p style={{ color: '#9ca3af', fontSize: '0.875rem', marginTop: 8, marginBottom: 0 }}>
              {isAuthenticatedForSession
                ? ` Authenticated for this session (${isDemoMode ? 'Demo Mode' : 'Live Mode'})`
                : ' These actions require steward authentication'}
            </p>
          </div>
        </ModalCard>
      )}
      </EnhancedModal>
    </>
  );
}

