import React, { useState } from 'react';
import { Settings, Pause, Play, RefreshCw, DollarSign, Percent, Clock, Shield } from 'lucide-react';
import EnhancedModal from './EnhancedModal';
import ModalCard from './ModalCard';
import { Button } from '../LandingPage';
import { useToast } from '../Toast';

const contractTypes = [
  { id: 'breeding', name: 'Breeding Lab', icon: '', color: '#22c55e' },
  { id: 'altar', name: 'Sacred Altar', icon: '', color: '#f59e0b' },
  { id: 'dao', name: 'DAO Governance', icon: '', color: '#3b82f6' },
  { id: 'offering', name: 'Sacred Offering', icon: '', color: '#ec4899' },
  { id: 'swap', name: 'Token Swap', icon: '', color: '#8b5cf6' },
  { id: 'farm', name: 'Mushroom Farm', icon: '', color: '#10b981' }
];

export default function ContractControlsModal({ isOpen, onClose, contracts = {} }) {
  const { toast } = useToast();
  const [selectedContract, setSelectedContract] = useState('breeding');
  const [loading, setLoading] = useState(false);

  const handlePause = async () => {
    setLoading(true);
    try {
      toast.info(`Pausing ${selectedContract} contract...`, { title: ' Pausing' });
      // TODO: Call contract.pause()
      await new Promise(r => setTimeout(r, 1000));
      toast.success(`${selectedContract} contract paused`, { title: ' Paused' });
    } catch (e) {
      toast.error(e.message, { title: 'Error' });
    } finally {
      setLoading(false);
    }
  };

  const handleUnpause = async () => {
    setLoading(true);
    try {
      toast.info(`Unpausing ${selectedContract} contract...`, { title: ' Unpausing' });
      // TODO: Call contract.unpause()
      await new Promise(r => setTimeout(r, 1000));
      toast.success(`${selectedContract} contract unpaused`, { title: ' Active' });
    } catch (e) {
      toast.error(e.message, { title: 'Error' });
    } finally {
      setLoading(false);
    }
  };

  const selectedInfo = contractTypes.find(c => c.id === selectedContract);

  return (
    <EnhancedModal
      isOpen={isOpen}
      onClose={onClose}
      title="Contract Controls"
      subtitle="Fine-tune contract parameters and manage state"
      icon={<Settings size={24} />}
      accentColor="#8b5cf6"
    >
      {/* Contract Selector */}
      <ModalCard title="Select Contract" icon={<Shield size={18} />}>
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(3, 1fr)',
          gap: '8px',
          marginBottom: '16px'
        }}>
          {contractTypes.map(contract => (
            <button
              key={contract.id}
              onClick={() => setSelectedContract(contract.id)}
              style={{
                padding: '12px 8px',
                borderRadius: '8px',
                border: selectedContract === contract.id
                  ? `2px solid ${contract.color}`
                  : '1px solid rgba(75, 85, 99, 0.4)',
                background: selectedContract === contract.id
                  ? `${contract.color}20`
                  : 'rgba(17, 24, 39, 0.6)',
                cursor: 'pointer',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                gap: '4px',
                transition: 'all 0.2s ease'
              }}
            >
              <span style={{ fontSize: '1.5rem' }}>{contract.icon}</span>
              <span style={{ fontSize: '0.75rem', color: '#d1d5db' }}>{contract.name}</span>
            </button>
          ))}
        </div>
      </ModalCard>

      {/* Pause/Unpause Controls */}
      <ModalCard
        title={`${selectedInfo?.icon} ${selectedInfo?.name} Controls`}
        icon={<Settings size={18} />}
      >
        <div style={{ display: 'flex', gap: '12px', marginBottom: '16px' }}>
          <Button
            onClick={handlePause}
            disabled={loading}
            style={{
              flex: 1,
              background: 'linear-gradient(135deg, rgba(239,68,68,0.3), rgba(220,38,38,0.2))',
              border: '1px solid rgba(239,68,68,0.5)'
            }}
          >
            <Pause size={16} style={{ marginRight: 6 }} />
            Pause Contract
          </Button>
          <Button
            onClick={handleUnpause}
            disabled={loading}
            style={{
              flex: 1,
              background: 'linear-gradient(135deg, rgba(34,197,94,0.3), rgba(22,163,74,0.2))',
              border: '1px solid rgba(34,197,94,0.5)'
            }}
          >
            <Play size={16} style={{ marginRight: 6 }} />
            Unpause Contract
          </Button>
        </div>

        <p style={{ color: '#9ca3af', fontSize: '0.875rem', marginTop: '8px' }}>
           Pausing a contract will prevent all user interactions until unpaused.
        </p>
      </ModalCard>

      {/* Parameter Controls */}
      <ModalCard title="Contract Parameters" icon={<DollarSign size={18} />}>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
            <Percent size={16} style={{ color: '#9ca3af' }} />
            <span style={{ color: '#d1d5db', flex: 1 }}>Fee Percentage</span>
            <input
              type="number"
              placeholder="0.3"
              style={{
                width: '80px',
                padding: '8px',
                borderRadius: '6px',
                border: '1px solid rgba(75, 85, 99, 0.4)',
                background: 'rgba(17, 24, 39, 0.6)',
                color: '#fff'
              }}
            />
            <span style={{ color: '#9ca3af' }}>%</span>
          </div>
        </div>
        <p style={{ color: '#6b7280', fontSize: '0.75rem', marginTop: '12px', fontStyle: 'italic' }}>
          Parameter controls coming soon - will allow fine-tuning of contract settings
        </p>
      </ModalCard>

      {/* Sale Configuration */}
      <ModalCard title="Sale Configuration" icon={<Clock size={18} />}>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
          {/* Token Price */}
          <div>
            <label style={{ color: '#d1d5db', fontSize: '0.875rem', marginBottom: '6px', display: 'block' }}>
              Token Price (ETH per token)
            </label>
            <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
              <input
                type="number"
                placeholder="0.001"
                step="0.0001"
                style={{
                  flex: 1,
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '1px solid rgba(75, 85, 99, 0.4)',
                  background: 'rgba(17, 24, 39, 0.6)',
                  color: '#fff'
                }}
              />
              <span style={{ color: '#9ca3af' }}>ETH</span>
            </div>
          </div>

          {/* Min/Max Purchase */}
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px' }}>
            <div>
              <label style={{ color: '#d1d5db', fontSize: '0.875rem', marginBottom: '6px', display: 'block' }}>
                Min Purchase
              </label>
              <input
                type="number"
                placeholder="0.01"
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '1px solid rgba(75, 85, 99, 0.4)',
                  background: 'rgba(17, 24, 39, 0.6)',
                  color: '#fff'
                }}
              />
            </div>
            <div>
              <label style={{ color: '#d1d5db', fontSize: '0.875rem', marginBottom: '6px', display: 'block' }}>
                Max Purchase
              </label>
              <input
                type="number"
                placeholder="10"
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '1px solid rgba(75, 85, 99, 0.4)',
                  background: 'rgba(17, 24, 39, 0.6)',
                  color: '#fff'
                }}
              />
            </div>
          </div>

          {/* Base URI */}
          <div>
            <label style={{ color: '#d1d5db', fontSize: '0.875rem', marginBottom: '6px', display: 'block' }}>
              NFT Base URI
            </label>
            <input
              type="text"
              placeholder="ipfs://QmYourCID/"
              style={{
                width: '100%',
                padding: '10px 12px',
                borderRadius: '8px',
                border: '1px solid rgba(75, 85, 99, 0.4)',
                background: 'rgba(17, 24, 39, 0.6)',
                color: '#fff'
              }}
            />
          </div>

          <Button
            onClick={() => toast.info('Sale configuration update coming soon', { title: ' Configuration' })}
            style={{
              background: 'linear-gradient(135deg, rgba(139,92,246,0.3), rgba(124,58,237,0.2))',
              border: '1px solid rgba(139,92,246,0.5)'
            }}
          >
            <RefreshCw size={16} style={{ marginRight: 6 }} />
            Update Configuration
          </Button>
        </div>
        <p style={{ color: '#6b7280', fontSize: '0.75rem', marginTop: '12px', fontStyle: 'italic' }}>
           Changes require owner signature and will be applied on-chain
        </p>
      </ModalCard>
    </EnhancedModal>
  );
}

