/**
 * Merkle Tree Utilities for Ethereal Offering
 *
 * The Mycelial Merkle Root - A living network of cryptographic verification
 *
 * In the mycelial analogy:
 * - Leaf nodes = nutrient packets (individual data entries)
 * - Branch nodes = mycelial cords combining data strands
 * - Merkle root = the "Mother Mycelium" encoding the entire network's state
 * - Merkle proof = tracing a single path through the mycelium to verify inclusion
 */

import { ethers } from 'ethers';

/**
 * Hash a single leaf (address) using keccak256
 * @param {string} address - Ethereum address
 * @returns {string} - Hashed leaf
 */
export function hashLeaf(address) {
  // Normalize address to lowercase
  const normalized = address.toLowerCase();
  // Pack and hash the address using ethers v5 syntax
  return ethers.utils.keccak256(
    ethers.utils.solidityPack(['address'], [normalized])
  );
}

/**
 * Hash two nodes together to form a parent
 * @param {string} left - Left child hash
 * @param {string} right - Right child hash
 * @returns {string} - Parent hash
 */
export function hashPair(left, right) {
  // Sort the pair to ensure consistent ordering (important for verification)
  const sorted = [left, right].sort();
  // Concatenate and hash using ethers v5 syntax
  return ethers.utils.keccak256(
    ethers.utils.concat([sorted[0], sorted[1]])
  );
}

/**
 * Build a Merkle Tree from a list of addresses
 * @param {string[]} addresses - Array of Ethereum addresses
 * @returns {Object} - Tree object with layers and root
 */
export function buildMerkleTree(addresses) {
  if (!addresses || addresses.length === 0) {
    return { layers: [], root: null, leaves: [] };
  }

  // Hash all leaves
  const leaves = addresses.map(addr => hashLeaf(addr));

  // Build tree layers
  const layers = [leaves];
  let currentLayer = leaves;

  while (currentLayer.length > 1) {
    const nextLayer = [];
    for (let i = 0; i < currentLayer.length; i += 2) {
      const left = currentLayer[i];
      const right = currentLayer[i + 1] || left; // Duplicate last element if odd
      nextLayer.push(hashPair(left, right));
    }
    layers.push(nextLayer);
    currentLayer = nextLayer;
  }

  return {
    layers,
    root: currentLayer[0] || null,
    leaves,
    addresses: addresses.map(a => a.toLowerCase())
  };
}

/**
 * Generate a Merkle proof for a specific address
 * @param {Object} tree - Merkle tree object from buildMerkleTree
 * @param {string} address - Address to generate proof for
 * @returns {Object} - Proof object with path and indices
 */
export function generateProof(tree, address) {
  const normalizedAddress = address.toLowerCase();
  const leafIndex = tree.addresses.indexOf(normalizedAddress);

  if (leafIndex === -1) {
    return { valid: false, proof: [], leaf: null, root: tree.root };
  }

  const proof = [];
  let index = leafIndex;

  for (let i = 0; i < tree.layers.length - 1; i++) {
    const layer = tree.layers[i];
    const isRightNode = index % 2 === 1;
    const siblingIndex = isRightNode ? index - 1 : index + 1;

    if (siblingIndex < layer.length) {
      proof.push({
        hash: layer[siblingIndex],
        position: isRightNode ? 'left' : 'right'
      });
    }

    index = Math.floor(index / 2);
  }

  return {
    valid: true,
    proof,
    leaf: tree.leaves[leafIndex],
    root: tree.root
  };
}

/**
 * Verify a Merkle proof
 * @param {string} leaf - Hashed leaf
 * @param {Array} proof - Array of proof elements
 * @param {string} root - Expected root
 * @returns {boolean} - Whether the proof is valid
 */
export function verifyProof(leaf, proof, root) {
  let computedHash = leaf;

  for (const element of proof) {
    if (element.position === 'right') {
      computedHash = hashPair(computedHash, element.hash);
    } else {
      computedHash = hashPair(element.hash, computedHash);
    }
  }

  return computedHash === root;
}

/**
 * Export Merkle data to JSON format for storage/sharing
 * @param {Object} tree - Merkle tree object
 * @param {string} treeType - Type identifier (e.g., 'breeding', 'dao')
 * @returns {Object} - Exportable JSON object
 */
export function exportMerkleData(tree, treeType) {
  return {
    type: treeType,
    root: tree.root,
    addressCount: tree.addresses.length,
    timestamp: Date.now(),
    version: '1.0.0'
  };
}

