import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Lock, Unlock, AlertTriangle, HelpCircle, ToggleLeft, ToggleRight } from 'lucide-react';
import { Button } from './LandingPage';

// Environment configuration - all passwords come from environment variables
// REACT_APP_ADMIN_PASSWORD: Production admin password (required in live mode)
// REACT_APP_DEMO_PASSWORD: Demo password for testing (defaults to a safe value)
const ADMIN_PASSWORD = process.env.REACT_APP_ADMIN_PASSWORD || 'demo';
const DEMO_PASSWORD = process.env.REACT_APP_DEMO_PASSWORD || 'demo_mode_2025';

/**
 * AuthenticationGate - Reusable component for protecting actions with password
 *
 * Props:
 * - isOpen: boolean - Whether the gate UI is shown
 * - onAuthenticated: (demoMode: boolean) => void - Called when authentication succeeds
 * - onCancel: () => void - Called when user cancels
 * - accentColor: string - Theme color (default: #a78bfa)
 * - title: string - Gate title
 * - description: string - Description of what's being protected
 */
export default function AuthenticationGate({
  isOpen,
  onAuthenticated,
  onCancel,
  accentColor = '#a78bfa',
  title = 'Steward Authentication Required',
  description = 'This action requires steward-level access. Enter the passphrase to proceed.'
}) {
  const [password, setPassword] = useState('');
  const [authError, setAuthError] = useState('');
  const [isDemoMode, setIsDemoMode] = useState(true);

  const handleAuthenticate = () => {
    // Check password - in demo mode, accept either password
    const isValidPassword = isDemoMode
      ? (password === ADMIN_PASSWORD || password === DEMO_PASSWORD)
      : password === ADMIN_PASSWORD;

    if (isValidPassword) {
      setPassword('');
      setAuthError('');
      onAuthenticated(isDemoMode);
    } else {
      setAuthError('Invalid password. Contact a steward for access.');
    }
  };

  const handleCancel = () => {
    setPassword('');
    setAuthError('');
    onCancel();
  };

  if (!isOpen) return null;

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0,0,0,0.7)',
          backdropFilter: 'blur(4px)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 99999, // Must be above modal content (10002) and all other overlays
          padding: 20
        }}
        onClick={handleCancel}
      >
        <motion.div
          initial={{ scale: 0.9, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          exit={{ scale: 0.9, opacity: 0 }}
          onClick={(e) => e.stopPropagation()}
          style={{
            background: 'linear-gradient(135deg, rgba(17,24,39,0.98), rgba(31,41,55,0.98))',
            border: `2px solid ${accentColor}60`,
            borderRadius: 16,
            padding: 24,
            maxWidth: 450,
            width: '100%',
            boxShadow: `0 0 40px ${accentColor}40`
          }}
        >
          {/* Header */}
          <div style={{ display: 'flex', alignItems: 'center', gap: 12, marginBottom: 16 }}>
            <div style={{
              width: 44,
              height: 44,
              borderRadius: '50%',
              background: `${accentColor}25`,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}>
              <Lock size={22} color={accentColor} />
            </div>
            <div>
              <h3 style={{ color: accentColor, fontWeight: 700, margin: 0, fontSize: '1.1rem' }}>
                 {title}
              </h3>
            </div>
          </div>

          <p style={{ color: '#d1d5db', marginBottom: 16, lineHeight: 1.6 }}>
            {description}
          </p>

          {/* Demo/Live Toggle */}
          <div
            onClick={() => setIsDemoMode(!isDemoMode)}
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              padding: '12px 16px',
              background: isDemoMode ? 'rgba(34,197,94,0.15)' : 'rgba(239,68,68,0.15)',
              border: `1px solid ${isDemoMode ? 'rgba(34,197,94,0.4)' : 'rgba(239,68,68,0.4)'}`,
              borderRadius: 10,
              marginBottom: 16,
              cursor: 'pointer',
              transition: 'all 0.3s ease'
            }}
          >
            <div>
              <div style={{ fontWeight: 600, color: isDemoMode ? '#4ade80' : '#f87171', marginBottom: 2 }}>
                {isDemoMode ? ' Demo Mode' : ' Live Mode'}
              </div>
              <div style={{ fontSize: '0.8rem', color: '#9ca3af' }}>
                {isDemoMode
                  ? 'Changes only affect local browser storage (testing)'
                  : 'Changes will be written to blockchain (production)'}
              </div>
            </div>
            {isDemoMode
              ? <ToggleLeft size={32} color="#4ade80" />
              : <ToggleRight size={32} color="#f87171" />}
          </div>

          {/* Password Input */}
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && handleAuthenticate()}
            placeholder="Enter steward passphrase..."
            autoComplete="off"
            data-1p-ignore="true"
            data-lpignore="true"
            data-form-type="other"
            style={{
              width: '100%',
              padding: '12px 16px',
              borderRadius: 8,
              border: `1px solid ${accentColor}80`,
              background: 'rgba(0,0,0,0.3)',
              color: 'white',
              fontSize: '1rem',
              boxSizing: 'border-box',
              marginBottom: 12
            }}
            autoFocus
          />

          {/* Error Message with Demo Password Hint */}
          <AnimatePresence>
            {authError && (
              <motion.div
                initial={{ opacity: 0, y: -10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                style={{
                  padding: 12,
                  background: 'rgba(239,68,68,0.2)',
                  border: '1px solid rgba(239,68,68,0.5)',
                  borderRadius: 8,
                  marginBottom: 12
                }}
              >
                <div style={{ color: '#fca5a5', display: 'flex', alignItems: 'center', gap: 8, marginBottom: 8 }}>
                  <AlertTriangle size={16} />
                  {authError}
                </div>
                {isDemoMode && (
                  <div style={{ display: 'flex', alignItems: 'center', gap: 6, color: '#9ca3af', fontSize: '0.85rem' }}>
                    <HelpCircle size={14} />
                    Demo Password: <code style={{ color: accentColor, background: 'rgba(0,0,0,0.3)', padding: '2px 6px', borderRadius: 4 }}>{DEMO_PASSWORD}</code>
                  </div>
                )}
              </motion.div>
            )}
          </AnimatePresence>

          {/* Action Buttons */}
          <div style={{ display: 'flex', gap: 12, justifyContent: 'center' }}>
            <Button
              onClick={handleCancel}
              style={{
                padding: '10px 20px',
                background: 'rgba(107,114,128,0.3)',
                border: '1px solid rgba(107,114,128,0.5)'
              }}
            >
              Cancel
            </Button>
            <Button
              onClick={handleAuthenticate}
              style={{
                padding: '10px 20px',
                background: `linear-gradient(135deg, ${accentColor}40, ${accentColor}20)`,
                border: `1px solid ${accentColor}60`
              }}
            >
              <Unlock size={16} style={{ marginRight: 8 }} />
              Authenticate
            </Button>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}

