/**
 * Address Parser Utilities
 * Parses addresses from various file formats (CSV, TXT, etc.)
 */

/**
 * Validates an Ethereum address format
 * @param {string} address - The address to validate
 * @returns {boolean} - Whether the address is valid
 */
export function isValidAddress(address) {
  if (!address) return false;
  const cleaned = address.trim();
  // Basic Ethereum address validation (0x followed by 40 hex chars)
  return /^0x[a-fA-F0-9]{40}$/.test(cleaned);
}

/**
 * Parse addresses from file content
 * Supports various formats:
 * - One address per line
 * - Comma-separated
 * - Mixed (commas and newlines)
 * - With or without trailing commas
 *
 * @param {string} content - File content to parse
 * @returns {Object} - { valid: string[], invalid: string[], duplicates: string[] }
 */
export function parseAddressesFromContent(content) {
  if (!content) return { valid: [], invalid: [], duplicates: [] };

  // Split by newlines and commas, filter empty strings
  const rawAddresses = content
    .split(/[\n,]+/)
    .map(addr => addr.trim())
    .filter(addr => addr.length > 0);

  const valid = [];
  const invalid = [];
  const duplicates = [];
  const seen = new Set();

  for (const addr of rawAddresses) {
    const normalized = addr.toLowerCase();

    if (!isValidAddress(addr)) {
      invalid.push(addr);
      continue;
    }

    if (seen.has(normalized)) {
      duplicates.push(addr);
      continue;
    }

    seen.add(normalized);
    valid.push(addr);
  }

  return { valid, invalid, duplicates };
}

/**
 * Parse addresses from a File object
 * @param {File} file - The file to parse
 * @returns {Promise<Object>} - { valid, invalid, duplicates }
 */
export async function parseAddressesFromFile(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onload = (event) => {
      const content = event.target.result;
      resolve(parseAddressesFromContent(content));
    };

    reader.onerror = (error) => {
      reject(error);
    };

    reader.readAsText(file);
  });
}

/**
 * Export addresses to a downloadable file
 * @param {string[]} addresses - Array of addresses to export
 * @param {string} filename - Name for the downloaded file
 * @param {string} format - 'csv' | 'txt' | 'json'
 */
export function exportAddresses(addresses, filename = 'whitelist', format = 'txt') {
  let content;
  let mimeType;
  let extension;

  switch (format) {
    case 'csv':
      content = addresses.join(',\n');
      mimeType = 'text/csv';
      extension = 'csv';
      break;
    case 'json':
      content = JSON.stringify(addresses, null, 2);
      mimeType = 'application/json';
      extension = 'json';
      break;
    case 'txt':
    default:
      content = addresses.join('\n');
      mimeType = 'text/plain';
      extension = 'txt';
  }

  const blob = new Blob([content], { type: mimeType });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = `${filename}.${extension}`;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}

/**
 * Format date for display
 * @param {Date|string|number} date - Date to format
 * @returns {string} - Formatted date string
 */
export function formatDate(date) {
  const d = new Date(date);
  return d.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
}

